import * as moment from "moment";
// import { Guid } from "guid-typescript";
import { Helper } from "@core/helper/Helper";
import { VStringService } from "@core/index";

export class PaymentReceiptModel {
  constructor(adjustment_type) {
    this.adjustment_type = adjustment_type;
  }

  id: string = "";
  template_id: string = ""; //id mu bin lai
  template_code: string = ""; /* Mu s bin lai */
  template_type: string = "";
  status: string = "";
  receipt_date: string = "";
  receipt_type: number = null;
  receipt_series: string = ""; /* K hiu ha n */
  receipt_number: number = null;
  payment_method_name: string = "";
  payment_method_code: string = "";
  fee_name: string = "";
  total_payment: number = null;
  total_payment_in_word: string = "";
  payer_name: string = ""; //tn ngi thanh ton
  payer_address: string = ""; //a ch ngi thanh ton
  payer_email: string = "";// email ngi thanh ton
  payer_phone: string = ""; // s in thoi ngi thanh ton
  customer_code: string = ""; // m khch hng
  customer_name: string = ""; // tn khch hng
  currency_code: string = ""; // m loi tin
  currency_rate: number = null; // t gi
  destroy_date: string = null; // ngy hy bin lai
  destroy_reason: string = null; // l do hy bin lai
  display_currency = null;
  price: number = null;
  quantity: number = null;
  fee_code: string = null;
  decimal_places_json = null;
  issuer_name: string = null; // Tn nh cung cp ch k s
  adjustment_type: number; // loi iu chnh
  payer_tax_code: string = null;
  verify_code: string = null;
  number_of_vouchers: string = null;
  customer_id: string = null;

  receipt_fees = [
    {
      id: Helper.makeid(),
      fee_name: "",
      fee_code: "",
      unit_code: "",
      unit_name: "",
      quantity: null,
      price: null,
      amount: null,
      order: null,
      view_order: null,
      amount_after_vat: null
    },
  ];

  receipt_options = [];
  fillFormData(data) {
    this.id = data.id;
    this.template_id = data.template_id;
    this.template_code = data.template_code;
    this.status = data.status;
    this.receipt_date = data.receipt_date;
    this.receipt_type = data.receipt_type;
    this.receipt_series = data.receipt_series;
    this.receipt_number = data.receipt_number;
    this.payment_method_name = data.payment_method_name;
    this.payment_method_code = data.payment_method_code;
    this.fee_name = data.fee_name;
    this.total_payment = data.total_payment;
    this.total_payment_in_word = data.total_payment_in_word;
    this.payer_name = data.payer_name;
    this.payer_address = data.payer_address;
    this.payer_email = data.payer_email;
    this.payer_phone = data.payer_phone;
    this.customer_code = data.customer_code;
    this.customer_name = data.customer_name;
    this.currency_code = data.currency_code;
    this.currency_rate = data.currency_rate;
    this.destroy_date = data.destroy_date;
    this.destroy_reason = data.destroy_reason;
    this.price = data.price;
    this.quantity = data.quantity;
    this.fee_code = data.fee_code;
    this.decimal_places_json = data.decimal_places_json;
    this.issuer_name = data.issuer_name;
    this.adjustment_type = data.adjustment_type;
    this.payer_tax_code = data.payer_tax_code;
    this.verify_code = data.verify_code;
    this.number_of_vouchers = data.number_of_vouchers;
    this.customer_id = data.customer_id;
    this.receipt_options = data.receipt_options;
  }

  fillFormDieuChinh(data) {
    this.currency_code = data.currency_code || "VND";
    this.currency_rate = data.currency_rate || null;
    this.customer_name = data.customer_name;
  }

  fillFormThayThe(data) {
    this.currency_code = data.currency_code || "VND";
    this.currency_rate = data.currency_rate || null;
    this.customer_name = data.customer_name;
  }

  invoice_products_data() {
    let arr = [];
    for (let i = 0; i < 5; i++) {
      arr.push({
        id: Helper.makeid(),
        order: i + 1,
        view_order: i + 1,
        fee_code: "",
        fee_name: "",
        unit_code: "",
        unit_name: "",
        quantity: null,
        price: null,
        amount: null,
        amount_after_vat: null
      });
    }
    return arr;
  }

  setInvoice_products_data(data) {
    let arr = [];
    data.forEach((item) => {
      arr.push({
        id: Helper.makeid(),
        order: item.order, //index
        view_order: item.view_order,
        fee_code: item.fee_code,
        fee_name: item.fee_name,
        unit_code: item.unit_code !== null ? item.unit_code : null,
        unit_name: item.unit_name,
        quantity: item.quantity !== null ? parseFloat(item.quantity) : null,
        price: item.price !== null ? parseFloat(item.price) : null,
        amount: item.amount !== null ? parseFloat(item.amount) : null,
        amount_after_vat: item.amount_after_vat !== null ? parseFloat(item.amount_after_vat) : null,
      });
    });

    arr = arr.sort((itemA, itemB) => {
      return itemA.order - itemB.order;
    });

    let index = arr.length ? parseInt(arr.slice(-1)[0]["order"]) : 0;

    for (let i = 0; i < 5; i++) {
      arr.push({
        id: Helper.makeid(),
        order: index + 1,
        view_order: index + 1,
        fee_code: "",
        fee_name: "",
        unit_code: "",
        unit_name: "",
        quantity: null,
        price: null,
        amount: null,
        amount_after_vat: null
      });
      index++;
    }

    return arr;
  }

  add_receipt_fee_data(view_order, order) {
    return {
      id: Helper.makeid(),
      order: order,
      fee_code: "",
      fee_name: "",
      unit_code: "",
      unit_name: "",
      quantity: null,
      price: null,
      amount: null,
      view_order: view_order,
      amount_after_vat: null
    };
  }

  tempImportRow(countRow, isPepsi = false) {
    let arr = [];
    for (let i = 0; i < countRow; i++) {
      arr.push({
        number_of_vouchers: "", // s phiu chng t
        adjustment_form: "" /* Phng thc iu chnh */,
        payer_tax_code: "", //M s thu
        total_payment: null,
        template_code: "",
        receipt_series: "",
        payer_name: "", //Tn ngi mua
        customer_name: "", //Tn n v (Tn khch hng)
        payer_address: "", //a ch
        payer_phone: "", //in thoi lin h
        payer_email: "", //Email
        payment_method_code: "", //Hnh thc thanh ton
        fee_code: "",
        fee_name: "",
        unit_code: "",
        number_code: "",
        unit_name: "",
        quantity: null,
        price: null,
        amount: null,
        amount_after_vat: null,
        id: null,
        order: i + 1,
        view_order: i + 1,
        dataType: "KHACH_HANG",
        statusData: "INVALID",
        creat_from: "model",
        receipt_options: null,
      });
    }
    return arr;
  }
  addImportRow(order, view_order) {
    let arr = {
      number_of_vouchers: "", // s phiu chng t
        adjustment_form: "" /* Phng thc iu chnh */,
        payer_tax_code: "", //M s thu
        total_payment: null,
        template_code: "",
        receipt_series: "",
        payer_name: "", //Tn ngi mua
        customer_name: "", //Tn n v (Tn khch hng)
        payer_address: "", //a ch
        payer_phone: "", //in thoi lin h
        payer_email: "", //Email
        payment_method_code: "", //Hnh thc thanh ton
        fee_code: "",
        fee_name: "",
        unit_code: "",
        number_code: "",
        unit_name: "",
        quantity: null,
        price: null,
        amount: null,
        amount_after_vat: null,
        id: null,
        order: order,
        view_order: view_order,
        dataType: "KHACH_HANG",
        statusData: "INVALID",
        creat_from: "model",
        receipt_options: null,
    };
    return arr;
  }
}
