  preview() {
    let convert = this.convertGirdToInvoices("preview");
    if (convert) {
      if (this.configs.system_parameter.display_template_from_server) {
        const initialState = {
          items: this.invoicesArr,
          isXuatHD: false,
          typeAction: 'NAP_EXCEL'
        };
        this.bsModalViewModel = this.modalImportService.show
          (
            ViewInvoiceComponent,
            {
              backdrop: 'static',
              keyboard: false,
              class: 'modal-xl view-invoice',
              initialState
            }
          );
        const subModal = this.modalImportService.onHide.subscribe(() => {
          subModal.unsubscribe();
        });
      }
      else {
        this.checkCKS("XEM_TRUOC");
      }
    }
  }

