import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';

import {IndexComponent} from './components/index/index.component';
import {InvoiceComponent} from './components/invoice/invoice.component';

// import { CreateInvoiceGuard } from '@shared/index';
import {PaymentReceiptResolver, ShowPaymentReceiptResolver} from './shared';
import {VatDeactivateGuard} from '@modules/vat/deactivate/vat-deactivate.guard';

const routes: Routes = [
  {
    path: '',
    component: IndexComponent
  },
  {
    path: 'create',
    component: InvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: {init: PaymentReceiptResolver},
    data: {brec: 'lap_bien_lai'}
  },
  {
    path: ':receiptId',
    component: InvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: {init: ShowPaymentReceiptResolver},
    data: {brec: 'chi_tiet_bien_lai'}
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class routing {
}
