import { AfterViewInit, ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import * as moment from 'moment';
import { Base64 } from 'js-base64';
import { AuthService, Helper, Library, sEnum, SocketService, VChanggeMoneyService, VPreviewInvService, VStringService } from '@core/index';
import { ProductGripComponent } from '../product-grip/product-grip.component';
import { FormInvoiceComponent } from '../form-invoice/form-invoice.component';
import { InvoiceModel, VatService } from '@modules/vat/shared';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import ArrayStore from 'devextreme/data/array_store';
import DataSource from 'devextreme/data/data_source';
import { TranslateService } from '@ngx-translate/core';
import { CustomersService } from '@modules/customers/shared';
import { SendInvoicesComponent } from '../send-invoices/send-invoices.component';
import * as _ from 'lodash';
import { CanComponentDeactivate } from '@modules/vat/deactivate/vat-deactivate.guard';
import { Observable } from 'rxjs';
import { InvoicesService } from '@core/library/invoice/invoices.service';
import { String } from 'typescript-string-operations';
