  /* Ghi tm */
  ghiTam() {
    if (this.isGhiTamHangLoat) {
      this.frmData = this.receiptArray[this.countReceiptGhiTam - 1];
      this.percentComplete = 100 / this.totalRecepitGhiTam;
    }
    this.isDisable = true
    if (!this.isGhiTamHangLoat)
      Library.showloading();
    if (this.frmData.id) {
      const sub = this.paymentRService.edit(this.frmData.id, this.frmData).subscribe(rs => {
        if (rs.status === 'success') {
          this.frmData.id = rs.data.id;
          if (this.isGhiTamHangLoat) {
            this.valueProgress += this.percentComplete;
            if (this.countReceiptGhiTam < this.totalRecepitGhiTam) {
              this.countReceiptGhiTam++;
              this.ghiTam();
            }
            else if (this.countReceiptGhiTam == this.totalRecepitGhiTam) {
              this.isGhiTamHangLoat = false;
              this.showPopupImport = false;
              Library.notify(this._translate.NOTIFY.save_invoice_success, 'success');
            }
          }
          else {
            Library.notify(this._translate.NOTIFY.save_invoice_success, 'success');
            this.setDataSessionStorage(this.frmData);
          }
        } else {
          Library.notify(rs.message, 'error');
        }
      }, err => {
      }, () => {
        if (!this.isGhiTamHangLoat)
          Library.hideloading();
        this.isDisable = false;
        sub.unsubscribe();
      });
    } else {
      const sub = this.paymentRService.ghiTam(this.frmData).subscribe(rs => {
        if (rs.status === 'success') {
          this.frmData.id = rs.data.id;
          if (this.isGhiTamHangLoat) {
            this.valueProgress += this.percentComplete;
            if (this.countReceiptGhiTam < this.totalRecepitGhiTam) {
              this.countReceiptGhiTam++;
              this.ghiTam();
            }
            else if (this.countReceiptGhiTam == this.totalRecepitGhiTam) {
              this.isGhiTamHangLoat = false;
              this.showPopupImport = false;
              Library.notify(this._translate.NOTIFY.save_invoice_success, 'success');
            }
          }
          else {
            Library.notify(this._translate.NOTIFY.save_invoice_success, 'success');
            this.setDataSessionStorage(this.frmData);
          }
        } else {
          Library.notify(rs.message, 'error');
          this.isDisable = false;
        }
      }, err => {
      }, () => {
        if (!this.isGhiTamHangLoat)
          Library.hideloading();
        this.isDisable = false;
        sub.unsubscribe();
      });
    }
  }

  xuatHoaDon() {
    if (this.isXuatHangLoat)
      this.frmData = this.receiptArray[this.countInvoice - 1];
    if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
      || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
      this.xuLiApiKhiXuat();
    } else {
      this.socketService.kyBienLai(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice');
    }
  }

  taoDSBienLai(isXuatHoaDon) {
    this.receiptArray = [];
    this.idArray = [];
    this.receiptArray.push(this.frmData);
    for (let i = 1; i < this.so_luong_bien_lai; i++) {
      let frmDataClone = Object.assign({}, this.frmData);
      frmDataClone.id = null;
      if (frmDataClone.receipt_fees && frmDataClone.receipt_fees.length > 0) {
        frmDataClone.receipt_fees.forEach(item => {
          item.id = Helper.makeid();
        });
      }
      this.receiptArray.push(frmDataClone);
    }
    this.showPopupImport = true;
    this.countInvoice = 1;
    this.countReceiptGhiTam = 1;
    if (isXuatHoaDon) {
      this.totalInvoice = this.receiptArray.length;
      this.xuatHoaDon();
    }
    else {
      this.totalRecepitGhiTam = this.receiptArray.length;
      this.ghiTam();
    }
  }
