@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  @ViewChild(DxMenuComponent) dxMenu: DxMenuComponent;
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  @ViewChild('expandMenu') expandMenu;
  @ViewChild(DownloadPdfComponent) downloadPdf: DownloadPdfComponent;
  dataSource: any;
  selectedRows: Array<any> = [];
  selectedItems: Array<any> = [];
  bsModalRef: BsModalRef;
  bsModalImportRef: BsModalRef;
  bsModalSignRef: BsModalRef;
  listDM: any;
  pageSize: any;
  loaiHoaDon: string;
  test = true;
  isAccessExportInv = true;
  invoice_releases = [];
  templatesTT78 = [];
  infoDangKy: any = {};
  paramSearch: any;
  _language: string;
  _translate: any;
  checkSubmit: any;
  extras = [];
  data_extras_invoice_buy = [];
  extend = [];
  private wasInside = false;
  select: any = {};
  configs: any = {};
  disEditMulti = true;
  hoaDonImportChoXuat: any;
  footerSum = {
    summary: '',
    summary_deduction: '',
  };
  objPXK = {};
  clickCurent = '';
  dataInfor: any;
  isSearchUI: boolean;
  isListHeader: boolean;
  dataAddInvoiceBuy = [];
  curentUser: any = {};
  taxCodeThueHCM = ["0102519041-555"];
  pageSelect: any;
  isTT78: boolean = false;
  isExistFormRelease: boolean = false;
  currentUser: any;
  isEFY: boolean = false;
  ioSignature: Subscription;

  valueProgress: number = 0;
  errorImport: boolean = false;
  subXuatHoaDon: any;
  showPopup = false;
  totalInvoice: number;
  invoiceIndex = 0;
  tempKiFile = 0;
  daXong: boolean = false;
  ganXong: boolean = false;
  invoices = [];
  isExportFailed: boolean = false;
  province_name: any;
  objStatusBtn = {};

  constructor(
    private authService: AuthService,
    private vatService: VatService,
    private modalService: BsModalService,
    private modalImportService: BsModalService,
    private router: Router,
    private route: ActivatedRoute,
    public translate: TranslateService,
    private socketService: SocketService,
    private spinner: NgxSpinnerService,
    private paymentRService: PaymentReceiptService,
    private vPreviewInvService : ViewInvoiceService,
    private invoiceService: InvoiceService,
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    this.curentUser = this.authService.getCurrentUser();
    translate.use(this._language);
    this.extras = [{
      id: '1',
      name: this._translate.CONTROLL.BUTTON.extend,
      items: [
        // {
        //   id: "NAP_HD_TU_EXCEL",
        //   name: this._translate.CONTROLL.BUTTON.import_invoice_from_excel,
        //   icon: this._translate.ICONS.excel
        // },
        {
          id: 'NAP_HD_TU_EXCEL_DT',
          name: 'Np bin lai t Excel',
          icon: this._translate.ICONS.excel
        },
        {
          id: 'SUA',
          name: this._translate.CONTROLL.BUTTON.edit,
          icon: this._translate.ICONS.edit
        },
        {
          id: 'XOA',
          name: this._translate.CONTROLL.BUTTON.delete,
          icon: this._translate.ICONS.delete
        },
        {
          id: 'XEM',
          name: 'Xem bin lai',
          icon: this._translate.ICONS.view
        },
        {
          id: 'GUI_BEN_MUA',
          name: 'Gi bin lai',
          icon: this._translate.ICONS.send_email
        },
        {
          id: 'TAI_HD_PDF',
          name: "Ti bin lai PDF",
          icon: this._translate.ICONS.download
        },
      ]
    }];
    this.data_extras_invoice_buy = [{
      id: '1',
      name: this._translate.CONTROLL.LABEL.export_data,
      items: [
        {
          id: 'LIST_INVOICE_BUY',
          name: this._translate.CONTROLL.LABEL.list_invoice_buy,
          icon: this._translate.ICONS.excel
        },
        {
          id: 'DETAILS_OF_EACH_GOODS_AND_SERVICE',
          name: this._translate.CONTROLL.LABEL.details_of_each_goods_and_service,
          icon: this._translate.ICONS.edit
        },
      ]
    }];
    this.dataAddInvoiceBuy = [{
      id: '1',
      name: this._translate.CONTROLL.BUTTON.add,
      items: [
        {
          id: 'ADD_ONE_INVOICE',
          name: this._translate.CONTROLL.LABEL.add_one_invoice,
          icon: this._translate.ICONS.add_new
        },
        {
          id: 'ADD_MULTIPLE_INVOICE',
          name: this._translate.CONTROLL.LABEL.add_multiple_invoice,
          icon: this._translate.ICONS.add_new
        },
      ]
    }];
    this.extend = [{
      id: '1',
      name: '',
      items: [
        {
          id: 'INCHUYENDOI',
          name: this._translate.CONTROLL.BUTTON.print_conversion,
          icon: 'assets/img/icons/In.png'
        },
      ]
    }];
  }
  ngOnInit() {
    this.handleUI(this.route.snapshot.data.type);
    this.configs = this.authService.getConfigs();
    if (!this.taxCodeThueHCM.includes(this.curentUser.account.tax_code)) {
      this.extras[0].items.push({ id: "TAI_HD_XML", name: 'Ti BL nh dng gc', icon: this._translate.ICONS.xml });
    }
    if (this.configs.system_parameter.allow_to_print_delivery_each_invoice)
      this.extras[0].items.push({ id: "IN_BB_GIAO_NHAN", name: this._translate.CONTROLL.BUTTON.print_delivery_minutes, icon: this._translate.ICONS.xml });
    if (this.curentUser.account.tax_code == "0102519041")
      this.isEFY = true;
    if (this.configs.system_parameter.select_invoice_current_page)
      this.pageSelect = "page";
    else
      this.pageSelect = "allPages";
    this.currentUser = this.authService.getCurrentUser();
    if (this.currentUser && this.currentUser.account && this.currentUser.account.van_account_info) {
      this.province_name = this.currentUser.account.van_account_info.city_name;
    }
    this.vatService.isCopy = false;
    this.vatService.data = null;
    const listpermissions = this.currentUser.user.permissions.split(',');
    const _obj = listpermissions.filter(p => p === 'phe_duyet');
    if (_obj.length > 0) {
      this.isAccessExportInv = true;
    } else {
      this.isAccessExportInv = false;
    }
    if (this.currentUser.is_convert_TT78) {
      this.infoDangKy = this.authService.getInfoRegister();
      if (this.infoDangKy && this.infoDangKy.id) {
        if (this.infoDangKy.status == "CHAP_NHAN") {
          this.isTT78 = true;
          let infoFormRelease = this.authService.getInfoFormRelease();
          if (infoFormRelease && infoFormRelease.invoice_releases && infoFormRelease.invoice_releases.length > 0) {
            let invoicesReleases = infoFormRelease.invoice_releases.filter(p => (parseInt(p.amount) - parseInt(p.used_amount)) > 0);
            if (invoicesReleases && invoicesReleases.length > 0)
              this.isExistFormRelease = true;
          }
        }
      }
    }
    else {
      // if (localStorage.getItem('new_member')) {
      //   Library.notify(this._translate.NOTIFY.you_must_create_a_release_notice_before_invoicing, 'warning');
      // }
    }
  }

  handleUI(value) {
    if (value === '03GTKT') {
      this.isSearchUI = false;
      this.isListHeader = false;
    } else {
      this.isSearchUI = true;
      this.isListHeader = true;
    }
    this.loaiHoaDon = value;
  }
  /* X l s kin click phn m rng ca tng ha n*/
  myFunction(id) {
    if (this.clickCurent !== '' && this.clickCurent !== id) {
      let valueCurrent = document.getElementById(this.clickCurent);
      if (!valueCurrent)
        document.getElementById(id).classList.toggle('show');
      else {
        document.getElementById(this.clickCurent).classList.remove('show');
        document.getElementById(id).classList.toggle('show');
      }
    }
    else
      document.getElementById(id).classList.toggle('show');
    this.clickCurent = id;

  }

  loadList(params?) {
    this.dataSource = {};
    const ref = {};
    if (params) {
      const _lst = this.paymentRService.getAll(this.loaiHoaDon, params);
      this.dataSource.store = _lst;
      this.footerSum = {
        summary: _lst.summary,
        summary_deduction: _lst.summary_deduction,
      };
    } else {
      this.dataSource.store = this.paymentRService.getAll(this.loaiHoaDon);
    }
  }

  onRowPrepared(e) {
    if (e.rowType === 'data') {
      switch (e.data.status) {
        case 'DA_XUAT':
          break;
        case 'GHI_TAM':
          if (e.data.receipt_number) {
            e.rowElement.style.color = 'black';
          } else {
            e.rowElement.style.color = '#DEA960';
          }
          break;
        case 'GIU_SO':
          e.rowElement.style.color = '#008B8B';
          break;
        default:
          break;
      }
      if (e.data.adjustment_type === '2' || e.data.adjustment_type === '7' || e.data.adjustment_type === '6') {
        const eles = e.rowElement.getElementsByTagName('td');
        for (let i = 0; i < eles.length; i++) {
          if (i !== 9 && i !== 1) {
            eles[i].style.textDecoration = 'line-through';
          }
        }
      }
    }
  }

  getSearch(e) {
    this.paramSearch = e;
    this.loadList(e);
  }

  checkDisableButton() {
    if (this.selectedItems.length) {
      if (this.selectedItems[0].status === 'DA_XUAT') {
        return true;
      }
      return false;
    }
    return false;
  }

  trangthaihoadon(data) {
    switch (data.status) {
      case 'DA_XUAT':
        break;
      case 'GHI_TAM':
        if (data.invoice_number) {
          return this._translate.GRID.INVOICE.signed_error;
        } else {
          return this._translate.CONTROLL.BUTTON.save_temporary;
        }
      case 'GIU_SO':
        return this._translate.GRID.INVOICE.wait_for_sign;
      default:
        break;
    }
  }

  // Chn i tng trong mn hnh danh sch
  selectionChanged(data: any) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    this.disEditMulti = true;
    this.selectedItems = data.selectedRowsData;
    // if (this.selectedItems.length > 0) {
    //   const checks = this.selectedItems.filter((item) => {
    //     return item.number_of_vouchers;
    //   });
    //   const checkDaXuat = this.selectedItems.filter((item) => {
    //     return item.status === 'DA_XUAT';
    //   });
    //   if (checks.length === this.selectedItems.length && checkDaXuat.length === 0) {
    //     this.disEditMulti = false;
    //   }
    //}
  }

  async lapHoaDon() {
    let isCheckInitFee = await this.authService.checkInitialFee(sEnum.initial_fee.bienlai);
    if (!isCheckInitFee)
      return;
    this.router.navigate(['system/payment-receipt/create']);
  }

  async copy(data) {
    let isCheckInitFee = await this.authService.checkInitialFee(sEnum.initial_fee.bienlai);
    if (!isCheckInitFee)
      return;
    const row = data ? data : this.selectedItems[0];
    let sub = this.paymentRService.show(row.id).subscribe(rs => {
      if (rs.status == 'success') {
        if (rs.data.receipt) {
          rs.data.receipt.id = null;
          //rs.data.receipt.origin_id = null;
          rs.data.receipt.adjustment_type = '1';
          rs.data.receipt.destroy_reason = null;
          rs.data.receipt.destroy_date = null;
          rs.data.receipt.receipt_number = null;
          rs.data.receipt.receipt_date = null;
          rs.data.receipt.status = rs.data.receipt.status == 'DA_XUAT' ? 'GHI_TAM' : rs.data.receipt.status;
          rs.data.receipt.updated_at = null;
          rs.data.receipt.user_id = null;
          rs.data.receipt.van_id = null;
          rs.data.receipt.status_order = null;
          rs.data.receipt.extend_json_invoice = null;
          rs.data.receipt.other_id = null;
          rs.data.receipt.verify_code = null;
          if (rs.data.receipt.receipt_options && rs.data.receipt.receipt_options.length > 0) {
            rs.data.receipt.receipt_options.forEach(item => {
              delete item['id'];
              delete item['receipt_id'];
            });
          }
          this.vatService.data = rs.data.receipt;
          this.vatService.isCopy = true;
        }
        this.router.navigate(['system/payment-receipt/create']);
      }
    });
  }

  async edit(data) {
    let isCheckInitFee = await this.authService.checkInitialFee(sEnum.initial_fee.bienlai);
    if (!isCheckInitFee)
      return;
    let rowData = null;
    if (data) {
      if (VStringService._getValueToString(data.status) == sEnum.statusInv.DaXuat)
        Library.notify(this._translate.THONG_TIN.khong_sua_duoc_bien_lai_da_xuat, 'error');
      else {
        rowData = data;
      }
    }
    else {
      if (this.selectedItems.length > 0) {
        if (this.selectedItems.length == 1)
          rowData = this.selectedItems[0];
        else {
          let _hdDaXuat = this.selectedItems.filter(p => p.status == sEnum.statusInv.DaXuat)[0];
          if (_hdDaXuat)
            Library.notify(this._translate.THONG_TIN.khong_sua_duoc_bien_lai_da_xuat, 'error');
          else {
            let _hdLapTrucTiep = this.selectedItems.filter(p => VStringService._getValueToString(p.number_of_vouchers) == '')[0];
            if (_hdLapTrucTiep)
              Library.notify(this._translate.NOTIFY.same_receipt_invoice, 'error');
            else {
              let invDefault = this.selectedItems[0];
              let invCompare = this.selectedItems.filter(p => p.template_id != invDefault.template_id)[0];
              if (invCompare)
                Library.notify(this._translate.NOTIFY.same_release_invoice, 'error');
              else {
                let arrStatusInv = ["GIU_SO", "GHI_TAM"];
                let arrStatusItem = this.selectedItems.map((obj: any) => obj.status);
                let result = arrStatusInv.every(val => arrStatusItem.includes(val));
                /* Kim tra ha n c sa phi cng trng thi (ghi tm hoc gi s) */
                if (result)
                  Library.notify(this._translate.NOTIFY.same_status_invoice, 'error');
                else
                  this.openImportForm(rowData);
              }
            }
          }
        }
      }
    }
    if (rowData) {
      this.router.navigate([`/system/payment-receipt/${VStringService._getValueToString(rowData.id)}`]);
    }
  }

  openImportForm(data) {
    Library.showloading();
    let _lst = [];
    if (data) {
      _lst.push(data.id);
    } else {
      _lst = this.selectedRows;
    }
    const subRoute = this.paymentRService.getEditImportReceipt(_lst, '', this.loaiHoaDon).subscribe((res: any) => {
      setTimeout(() => {
        this.select.products = res[0].data.products;
        this.hoaDonImportChoXuat = res[1].data.receipts.sort((itemA, itemB) => {
          return moment(itemA.created_at, 'YYYY-MM-DD HH:mm:ss').valueOf() - moment(itemB.created_at, 'YYYY-MM-DD HH:mm:ss').valueOf();
        });
        this.select.unit_codes = res[2].data.DM_DON_VI_TINH;
        this.select.payment_method_names = res[2].data.DM_HINH_THUC_THANH_TOAN;
        this.select.vats = res[2].data.DM_TY_LE_VAT;
        this.templatesTT78 = Helper.convertTemplatesToReceiptResolver(res[3]);
        this.hoaDonImportChoXuat.forEach(invoice => {
          invoice.receipt_fees.forEach(row => {
            if (row.fee_code) {
              const _obj = this.select.products.filter(p => p.code === row.fee_code)[0];
              if (!_obj) {
                const _objPush = {
                  code: row.fee_code,
                  name: row.fee_name,
                  mhvth: row.fee_code + ': ' + row.fee_name,
                };
                this.select.products.push(_objPush);
              }
            }
          });
        });
        let search = new Object;
        this.route.queryParams.subscribe(queryParams => {
          for (let key in queryParams) {
            if (queryParams.hasOwnProperty(key))
              search[key] = queryParams[key];
          }
        });
        search["idTempt"] = Helper.makeid();
        const initialState = {
          select: this.select,
          hoaDonImportChoXuat: this.hoaDonImportChoXuat,
          templatesTT78: this.templatesTT78,
          infoDangKy: this.infoDangKy,
          search: search
        };
        this.bsModalImportRef = this.modalImportService.show
          (
            ImportInvoicesDtComponent,
            {
              backdrop: 'static',
              keyboard: false,
              class: 'modal-lg full-width',
              initialState
            }
          );
        const subModal = this.modalImportService.onHide.subscribe(() => {
          this.loadList(this.paramSearch);
          subModal.unsubscribe();
        });
      }, 10);
      Library.hideloading();
    }, (err) => {
    }, () => {
      subRoute.unsubscribe();
    });
  }

  async editNotSelect(row) {
    let isCheckInitFee = await this.authService.checkInitialFee(sEnum.initial_fee.bienlai);
    if (!isCheckInitFee)
      return;
    if (row.status === 'DA_XUAT') {
      Library.notify(this._translate.THONG_TIN.khong_sua_duoc_bien_lai_da_xuat, 'error');
    } else {
      this.router.navigate([`/system/payment-receipt/${VStringService._getValueToString(row.id)}`]);
    }
  }

  async viewDetailInvoice(row) {
    let isCheckInitFee = await this.authService.checkInitialFee(sEnum.initial_fee.bienlai);
    if (!isCheckInitFee)
      return;
    this.router.navigate([`/system/payment-receipt/${VStringService._getValueToString(row.id)}`]);
  }

  async delete(data) {
    let isCheckInitFee = await this.authService.checkInitialFee(sEnum.initial_fee.bienlai);
    if (!isCheckInitFee)
      return;
    if (this.selectedItems.length || data) {
      const status = '';
      let ids = '';
      if (data) {
        ids = data.id;
      } else {
        ids = this.selectedRows.join(',');
      }
      const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
      result.then((dialogResult) => {
        if (dialogResult) {
          Library.showloading();
          this.paymentRService.delete(ids).subscribe(rs => {
            if (rs.status === 'success') {
              Library.notify(this._translate.NOTIFY.delete_success, 'success');
              this.loadList(this.paramSearch);
            } else {
              Library.notify(rs.message, 'error');
            }
          }, err => {
          }, () => {
            Library.hideloading();
          });
        }
      });
    }
  }

  giuSo() {
    Library.showloading();
    const sub = this.vatService.getTemplate().subscribe(rs => {
      if (rs.status === 'success') {
        const initialState = {
          invoice_releases: rs.data.invoice_releases
        };
        this.bsModalRef = this.modalService.show(KeepNumberComponent, {
          class: 'modal-lg', initialState, backdrop: 'static',
          keyboard: false
        });
        this.modalService.onHide.subscribe(() => {
          if (this.bsModalRef.content.successSaveItem) {
            this.loadList(this.paramSearch);
          }
        });
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }

  setUrlNavigate() {
    const url = window.location.pathname;
    if (sessionStorage.hasOwnProperty('urlIndex')) {
      if (sessionStorage.getItem('urlIndex') !== null) {
        sessionStorage.setItem('urlIndex', url);
      } else {
        sessionStorage.setItem('urlIndex', url);
      }
    } else {
      sessionStorage.setItem('urlIndex', url);
    }
  }
  view(data) {
    if (this.selectedItems.length > 0 || data) {
      const _lst = this.selectedItems.filter(p => p.status === 'DA_XUAT');
      if (_lst.length > 0 || data) {
        if (this.selectedItems.length === 1) {
          this.dataInfor = this.selectedItems[0];
        } else {
          this.dataInfor = data;
        }
        let ids = [];
        if (data) {
          ids.push(data.id);
        } else {
          ids = _lst.map(e => e.id);
        }
        if (this.configs.system_parameter.view_multi_invoice) {
          let data = this.paymentRService.taodulieuXemMau(ids);
          data['configs'] = sEnum.cauhinhxemmau.hoadon;
          this.vPreviewInvService.xemMau(data);
        }
        else {
          const initialState = {
            items: ids,
            isXuatHD: false,
            hinhthucxuat: '',
            typeAction: sEnum.typeCode.xem_bien_lai
          };
          this.vPreviewInvService.xemMauPhanTrang(initialState);
        }
      } else {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
      }
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }

  guibenmua(data) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    let check = true;
    if (data) {
      if (data.status !== 'DA_XUAT') {
        check = false;
      }
    } else {
      if (this.selectedItems.filter(p => p.status === 'DA_XUAT').length === 0) {
        check = false;
      }
    }

    if (check && (this.selectedItems.length || data)) {
      Library.showloading();
      let ids = [];
      if (data) {
        ids.push(data.id);
      } else {
        ids = this.selectedItems.filter(p => p.status === 'DA_XUAT').map(p => p.id);
      }
      this.paymentRService.layDanhSachBienLai(ids, this.loaiHoaDon).subscribe((rs: any) => {
        if (rs[0].status === 'success') {
          const initialState = {
            invoiceList: rs[0].data.receipts,
            configs: rs[1].data.configs,
            isBienLaiDienTu: true
          };
          this.bsModalSignRef = this.modalService.show(SendListEmailComponent, {
            backdrop: 'static',
            keyboard: false,
            class: 'modal-lg full-modal',
            initialState
          });
          const subModal = this.modalService.onHide.subscribe(() => {
            if (this.bsModalSignRef.content.successSaveItem) {
              this.selectedItems = [];
              this.selectedRows = [];
              this.loadList(this.paramSearch);
              subModal.unsubscribe();
            }
          });
        } else {
          Library.notify(this._translate.NOTIFY.an_error_occurred, 'error');
        }

      }, err => {
      }, () => {
        Library.hideloading();
      });
    } else {
      Library.notify(this._translate.NOTIFY.no_invoice_selected, 'error');
    }
  }

  viewAttachFile(data) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    if (data) {
      Library.showloading();
      this.vatService.showAttachFile(data.id).subscribe((rs: any) => {
        if (rs.status === 'success') {
          if (rs.data && rs.data.invoice && rs.data.invoice.invoice_files.length > 0) {
            const initialState = {
              fileData: rs.data.invoice.invoice_files,
              view_attach_file: true,
            };
            this.bsModalSignRef = this.modalService.show(ViewAttachFileComponent, {
              backdrop: 'static',
              keyboard: false,
              class: 'modal-lg',
              initialState
            });

          } else {
            Library.notify(this._translate.NOTIFY.not_file_attach, 'error');
          }

          // let subModal = this.modalService.onHide.subscribe(() => {
          //   if (this.bsModalSignRef.content.successSaveItem) {
          //     this.selectedItems = [];
          //     this.selectedRows = [];
          //     //this.loadList(this.paramSearch);
          //     subModal.unsubscribe();
          //   }
          // });
        } else {
          Library.notify(this._translate.NOTIFY.an_error_occurred, 'error');
        }

      }, err => {
      }, () => {
        Library.hideloading();
      });
    }
  }

  sendInvoice(id) {
    Library.showloading();
    let sub = this.paymentRService.showToSendReceipt(id).subscribe(rs => {
      if (rs.status == 'success') {
        const initialState = {
          receipt: rs.data.receipt,
        };
        this.bsModalRef = this.modalService.show(SendInvoicesComponent, {
          class: 'modal-lg full-modal', initialState, backdrop: 'static',
          keyboard: false
        });
        this.modalService.onHide.subscribe(() => {
          if (this.bsModalRef.content.successSaveItem) {
            this.loadList(this.paramSearch);
          }
        });
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }

  customizeText = (url) => {
    this.invoiceService.handleTotalGrid(url.value, this.objStatusBtn, this.dataGrid);
    return '';
  }

  kyHangLoat(data?) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    if (!InvoicesService.checkTimeExportInv(this.configs.system_parameter)) {
      Library.notify(String.Format(this._translate.NOTIFY.time_export_inv, moment(this.configs.system_parameter.time_start).format('HH:mm'), moment(this.configs.system_parameter.time_end).format('HH:mm')), 'error');
      return;
    }
    let check = true;
    if (data) {
      if (data.status !== 'GIU_SO') {
        check = false;
      }
    } else {
      this.selectedItems.forEach(item => {
        if (item.status !== 'GIU_SO') {
          check = false;
        }
      });
    }
    if (check && (this.selectedItems.length || data)) {
      // if (this.selectedItems.length > 50 && !data) {
      //   Library.notify(this._translate.NOTIFY.only_up_to_25_invoices_can_be_selected_for_sign, 'error');
      //   return false;
      // }
      Library.showloading();
      let ids = [];
      if (data) {
        ids.push(data.id);
      } else {
        ids = this.selectedRows;
      }
      this.vatService.getInvoicesWaitSign(ids, this.loaiHoaDon).subscribe((rs: any) => {
        if (rs[0].status === 'success') {
          const initialState = {
            invoiceList: rs[0].data.invoices,
            configs: rs[1].data.configs,
            isHdGTGT: this.loaiHoaDon === '01GTKT' ? true : false,
          };
          this.bsModalSignRef = this.modalService.show(SignManyComponent, {
            backdrop: 'static',
            keyboard: false,
            class: 'modal-lg full-modal',
            initialState
          });
          const subModal = this.modalService.onHide.subscribe(() => {
            if (this.bsModalSignRef.content.successSaveItem) {
              this.selectedItems = [];
              this.selectedRows = [];
              this.loadList(this.paramSearch);
              subModal.unsubscribe();
            }
          });
        } else {
          Library.notify(this._translate.NOTIFY.an_error_occurred, 'error');
        }

      }, err => {
      }, () => {
        Library.hideloading();
      });
    } else {
      Library.notify(this._translate.NOTIFY.no_invoice_waite_sign_selected, 'error');
    }
  }

  async napHDTuExcelDt() {
    let isCheckInitFee = await this.authService.checkInitialFee(sEnum.initial_fee.bienlai);
    if (!isCheckInitFee)
      return;
    Library.showloading();
    this.paymentRService.getImportReceipt(this.loaiHoaDon).subscribe((res: any) => {
      // Hng ha dch v
      this.templatesTT78 = Helper.convertTemplatesToReceiptResolver(res[2]);
      this.select.products = res[0].data.products;
      this.select.unit_codes = res[1].data.DM_DON_VI_TINH;
      this.select.payment_method_names = res[1].data.DM_HINH_THUC_THANH_TOAN;
      this.select.vats = res[1].data.DM_TY_LE_VAT;
      let search = new Object;
      this.route.queryParams.subscribe(queryParams => {
        for (let key in queryParams) {
          if (queryParams.hasOwnProperty(key))
            search[key] = queryParams[key];
        }
      });
      search["idTempt"] = Helper.makeid();
      const initialState = {
        select: this.select,
        isAccessExportInv: this.isAccessExportInv,
        templatesTT78: this.templatesTT78,
        infoDangKy: this.infoDangKy,
        search: search
      };
      this.bsModalImportRef = this.modalImportService.show(
        ImportInvoicesDtComponent,
        {
          backdrop: 'static',
          keyboard: false,
          class: 'modal-lg full-width',
          initialState
        }
      );
      const subModal = this.modalImportService.onHide.subscribe(() => {
        this.loadList(this.paramSearch);
        subModal.unsubscribe();
      });
      Library.hideloading();
    });
  }
  /* Ti file xml ha n*/
  taiHDXml() {
    if (this.selectedItems.length > 0) {
      const _lst = this.selectedItems.filter(p => p.status == sEnum.statusInv.DaXuat);
      if (_lst.length > 0) {
        let ids = [];
        ids = _lst.map(e => e.id);
        Library.showloading();
        let sub = this.paymentRService.xuatHDXml(ids).subscribe(rs => {
          if (rs.status == "success") {
            Helper.dowloadFile(rs.data.file_name, 'zip', rs.data.file_content);
            this.selectedItems = [];
            this.selectedRows = [];
          } else {
            Library.notify(rs.message, "error");
          }
        }, err => { }, () => {
          sub.unsubscribe();
          Library.hideloading();
        });
      }
      else {
        Library.notify(this._translate.NOTIFY.invoice_export_status, 'error');
        return;
      }
    }
    else {
      Library.notify(this._translate.NOTIFY.invoice_export_status, 'error');
      return;
    }
  }
  /* In bin bn tr hng */
  inBBTraHang(data) {
    if (data) {
      if (data.status != sEnum.statusInv.DaXuat) {
        Library.notify(this._translate.NOTIFY.not_print_delivery_record, 'error');
        return;
      }
      Library.showloading();
      let sub = this.vatService.xuatBBTraHang(data.id).subscribe(res => {
        if (res.status === 'success') {
          const file = res.data.invoice;
          Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
        } else {
          Library.notify(res.message, 'error');
        }
        Library.hideloading();
      }, err => { }, () => {
        sub.unsubscribe();
        Library.hideloading();
      });
    }
    else {
      Library.notify(this._translate.NOTIFY.not_print_delivery_record, 'error');
      return;
    }
  }
  taiHDPdf() {
    if (this.selectedItems.length > 0) {
      const _lst = this.selectedItems.filter(p => p.status == sEnum.statusInv.DaXuat);
      if (_lst.length > 0) {
        let ids = _lst.map(e => e.id);
        this.downloadPdf.downloadPdf(ids, 'receipts', 'receipt', false);
      }
      else {
        Library.notify(this._translate.NOTIFY.invoice_export_status, 'error');
        return;
      }
    }
    else {
      Library.notify(this._translate.NOTIFY.invoice_export_status, 'error');
      return;
    }
  }
  itemExtrasClick(e) {
    if (e.itemData.id === 'NAP_HD_TU_EXCEL_DT') {
      this.napHDTuExcelDt();
    } else if (e.itemData.id === 'GIU_SO') {
      this.giuSo();
    } else if (e.itemData.id === 'KY_SO') {
      this.kyHangLoat(null);
    } else if (e.itemData.id === 'SUA') {
      this.edit(null);
    } else if (e.itemData.id === 'XOA') {
      this.delete(null);
    } else if (e.itemData.id === 'XEM') {
      this.view(null);
    } else if (e.itemData.id === 'GUI_BEN_MUA') {
      this.guibenmua(null);
    } else if (e.itemData.id === 'LIST_INVOICE_BUY') {
      this.guibenmua(null);
    } else if (e.itemData.id === 'DETAILS_OF_EACH_GOODS_AND_SERVICE') {
      this.guibenmua(null);
    } else if (e.itemData.id === 'ADD_ONE_INVOICE') {
      this.router.navigate(['/system/invoice-buy/create']);
    } else if (e.itemData.id === 'ADD_MULTIPLE_INVOICE') {
      this.guibenmua(null);
    } else if (e.itemData.id == "TAI_HD_XML") {
      this.taiHDXml();
    } else if (e.itemData.id == "TAI_HD_PDF") {
      this.taiHDPdf();
    }
  }
  LydoKTiepNhan(data) {
    let lstErr = JSON.parse(data.error_content_invoice);
    const initialState = {
      lstErr: lstErr,
      notifyType: "THONG_BAO_LOI_CQT"
    };
    this.bsModalSignRef = this.modalService.show(SendDataForTaxComponent, {
      backdrop: 'static',
      keyboard: true,
      class: 'modal-lg modal-margin-width',
      initialState
    });
    const subModal = this.modalService.onHide.subscribe(() => {
      subModal.unsubscribe();
    });
  }
  ngOnDestroy() {
  }
  /* Trng thi thanh ton */
  trangthaithanhtoan(data) {
    if (Number.parseInt(data.invoice_number) > 0) {
      if (data.payment_status)
        return " thanh ton";
      else
        return "Cha thanh ton";
    }
    else
      return "";
  }
  iterator(a, take, skip) {
    let current = skip * 10;
    let l = a.length;
    let end = current + take;
    let part = a.slice(current, end);
    current = end < l ? end : 0;
    return part;
  }

  onContentReady(e) {
    if (e) {
      const dataSource = this.dataGrid.instance.getDataSource();
      this.pageSize = dataSource ? dataSource.items().length : 0;
    }
  }
}
