import { Injectable } from '@angular/core';
import { Router, Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { take, map } from 'rxjs/operators';
import { ListsService } from '@modules/lists/shared';

import { Library, Helper, sEnum, AuthService } from '@core/index';
import { forkJoin } from 'rxjs';
import { VatService } from '@modules/vat/shared';
import { PaymentReceiptService } from './payment-receipt.service';

@Injectable()
export class ShowPaymentReceiptResolver implements Resolve<any[]> {
  constructor(
    private vatService: VatService,
    private router: Router,
    private listsService: ListsService,
    private authService: AuthService,
    private paymentRService: PaymentReceiptService
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]> {
    let invoice_id = route.paramMap.get('receiptId');
    Library.showloading();
    let loaiHoaDon = route.data.type;
    let _countDm = 0;
    let dataJoin = [];
    let dataDM: any;
    /**
     * Thiu bn hc sinh sinh vin, r sot li cc hm lin quan
     */
    let arr = [
      {
        "code": "DM_LOAI_TIEN",
        "order": 0
      },
      {
        "code": "DM_HINH_THUC_THANH_TOAN",
        "order": 0
      },
      {
        "code": "DM_DON_VI_TINH",
        "order": 0
      },
      {
        "code": "DM_TY_LE_VAT",
        "order": 0
      },
    ];
    dataDM = this.listsService.getDMBy(arr);
    if (dataDM.length > 0) {
      dataDM.forEach(element => {
        dataJoin.push(element);
      });
    }
    _countDm = dataDM.length;
    let dataRecord: any;
    dataRecord = this.paymentRService.showPaymentReceiptResolver(invoice_id, loaiHoaDon);
    if (dataRecord.length > 0) {
      dataRecord.forEach(element => {
        dataJoin.push(element);
      });
    }
    let dataForkJoin = forkJoin(dataJoin);
    return dataForkJoin.pipe(
      take(1),
      map(rs => {
        Library.hideloading();
        let isEdit = true;
        let status = sEnum.statusApi.success;
        for (let index = 0; index < rs.length; index++) {
          if (rs[index].status != sEnum.statusApi.success) {
            Library.notify(rs[index].message, "error");
            this.router.navigate(['/system/payment-receipt']);
            status = sEnum.statusApi.error;
            break;
          }
        }
        if (status == sEnum.statusApi.success) {
          if (dataDM.length > 0) {
            let _data = [];
            rs.forEach((element, i) => {
              if (i < dataDM.length) {
                _data.push(element);
              }
            });
            this.authService.setDMLocal(_data);
          }
          let templates = Helper.convertTemplatesToReceiptResolver(rs[dataJoin.length - 1]);
          return [
            rs[_countDm].data.products,
            rs[dataJoin.length - 2].data.receipt,
            isEdit,
            templates,
          ];
        }
        else
          return null;
      })
    );
  }
}